'use client';
import { SpotifyApi } from '@spotify/web-api-ts-sdk';
import { scopes } from '../authenticate/page';
import { useEffect } from 'react';
// import { initClient } from './client';

export default function MyPlaylists() {
	const sdk = SpotifyApi.withUserAuthorization(
		'0916ebedc8fe494699b4252738eb2444',
		'http://localhost:3000/my-playlists',
		scopes
	);
	useEffect(() => {
		const getUser = async () => {
			const user = await sdk.currentUser.profile();
			console.log('ðŸš€ ~ getUser ~ user:', user);
			const playlists = await sdk.playlists.getUsersPlaylists(user.id);
			console.log('ðŸš€ ~ getUser ~ playlists:', playlists);
		};
		getUser();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);
	return <div>My Playlists</div>;
}
